import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { privacyPolicyApi } from '../../apis/services'
import Spinner from '../Spinner';
import { errorResponseHandler } from '../../apis/http';


const PrivacyPolicy = () => {
    const [data, setData] = useState();
    const [loading, setLoading] = useState(true)
    const fetchPrivacyPolicy = async () => {

        try {
            const response = await privacyPolicyApi();
            setData(response.privacypolicy?.[0]?.content)
        } catch (error) {
            errorResponseHandler(error)

        } finally {
            setLoading(false)
        }

    }
    useEffect(() => {

        fetchPrivacyPolicy()
    }, [])
    if (loading) {
        return <Spinner />
    }
    return (
        <>
            <input type="checkbox" id="mpclose" />
        
            <div className="page-header bg-section dark-section">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-12">
                            <div className="page-header-box">
                                <h1 className="text-anime-style-3" data-cursor="-opaque">Privacy Policy</h1>
                                <nav className="wow fadeInUp">
                                    <ol className="breadcrumb">
                                        <li className="breadcrumb-item"><Link to="/">home </Link> </li>
                                        <li className="breadcrumb-item"><i className="fa fa-chevron-right" aria-hidden="true"></i></li>
                                        <li className="breadcrumb-item active" aria-current="page">Privacy Policy</li>
                                    </ol>
                                </nav>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="our-approach bg-section" style={{ background: "#EAEEF1" }}>
                <div className="container">
                    <div className="row align-items-center">
                        <div className="col-lg-12">
                            <div className="our-approach-content">
                                <div className="section-title">
                                    <h2 className="text-anime-style-3" data-cursor="-opaque">Privacy Policy</h2>
                                    <div dangerouslySetInnerHTML={{ __html: data }} />

                                </div>
                            </div>


                        </div>

                    </div>
                </div>
            </div>
        </>)
}

export default PrivacyPolicy